/*
---------------
Layout 
---------------
*/

body {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  max-width: 100vw;

  font-family: 'robotoregular', sans-serif;
  font-size: var(--step-0);
}

/* main heading */

.main-heading-container {
  grid-column: span 2;

  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;

  background: #7189ff;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);
}

.main-heading-and-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
}

.main-heading-container h1 {
  font-family: 'reenie_beanieregular', sans-serif;
  font-size: var(--step-6);
  font-weight: 500;
  text-align: center;
}

.main-heading-container .list-icon {
  width: 100px;
}

/*
---------------
Navigation
---------------
*/

/* hamburger on main heading */

.navbar-toggle {
  display: none;
  background: transparent;
  padding: 10px;
  border: none;
  cursor: pointer;
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;

  background-color: #111;
  transition: all 0.3 ease-in-out;
}

/* nav on left side */

nav {
  padding: 2rem;

  background: #624cab;
  color: #fff;
}

nav h2 {
  font-size: var(--step-2);
  font-weight: 500;

  border-bottom: 2px solid #fff;
}

.add-todo-icon-and-btn,
.add-project-icon-and-btn {
  margin: 1.25rem 0;
}

.add-todo-btn,
.add-project-btn {
  font-size: var(--step-1);
  font-weight: 700;
}

.nav-project-heading {
  margin-top: 4rem;
}

nav .feather-icon {
  width: 32px;
  height: 32px;
}

.feather-icon {
  cursor: pointer;
}

nav button {
  background: transparent;
  border: none;
  color: #fff;
  cursor: pointer;
}

nav li {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin: 0.25rem 0;
  padding: 0.5rem 1rem;
  width: max-content;

  border-radius: 30px;
}

nav li:hover {
  background-color: #7189ff;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);
}

.add-todo-icon-and-btn:hover,
.add-project-icon-and-btn:hover {
  background-color: #d81159;
}

.active-btn-container.active {
  background-color: #7189ff;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);
}

/*
---------------
Todos Content
---------------
*/

.category-heading {
  margin: 3rem 0;
  text-align: center;
  font-size: var(--step-4);

  color: #624cab;
}

.project-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  margin: 3rem 0 2rem 0;

  font-family: 'reenie_beanieregular', sans-serif;
}

.project-heading {
  font-size: var(--step-4);
  font-weight: 500;
}

.todo-container {
  display: grid;
  grid-template-columns: auto 1fr auto auto;
  align-items: center;
  gap: 2rem;
  padding: 1rem 2rem;

  border-bottom: 2px solid #a0ddff;
}

.todo-icons-container,
.project-header-icons-container {
  display: flex;
  gap: 0.5rem;
}

.completed {
  color: #c1cefe;
  text-decoration: line-through;
}

input[type='checkbox'] {
  appearance: none;
  color: currentColor;
  width: 1.5em;
  height: 1.5em;
  border: 0.15em solid currentColor;
  border-radius: 50%;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;

  cursor: pointer;
}

input[type='checkbox']::before {
  content: '';
  width: 0.65em;
  height: 0.65em;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
  transform: scale(0);
  transform-origin: bottom left;
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--form-control-color);
  background-color: #c1cefe;
}

input[type='checkbox']:checked::before {
  transform: scale(1);
}

.important {
  color: #d81159;
}

.completed {
  color: #c1cefe;
  text-decoration: line-through;
}

.description-container {
  background-color: #a0ddff;
  padding: 1rem 2rem;
}

.hide {
  display: none;
}

.chevron-icon-container {
  transition: transform 0.3s ease; /* smooth transition for the flip in class .flip-icon */
}

.flip-icon {
  transform: scaleY(-1); /* vertical flip */
}

.empty {
  text-align: center;
}

#content {
  margin-bottom: 3rem;
}

/*
---------------
Footer
---------------
*/

footer {
  grid-column: span 2;
  padding: 0.5rem;
  text-align: center;

  background-color: #c1cefe;
}

footer a {
  color: #fff;
}

/*
---------------
Forms
---------------
*/

dialog {
  padding: 2rem;

  border-radius: 30px;
  border: none;
  background-color: #7189ff;
  color: #fff;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);
}

form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 0.5rem;
}

form h3 {
  margin-bottom: 1rem;
  font-size: var(--step-2);
  text-align: center;
}

input,
select,
textarea {
  padding: 0.15rem 0.5rem;

  border-radius: 30px;
  border: 3px solid #fff;
}

textarea {
  border-radius: 10px;
}

textarea::-webkit-scrollbar {
  width: 8px;
}

textarea::-webkit-scrollbar-thumb {
  background-color: #c1cefe;
  border-radius: 10px;
}

textarea::-webkit-scrollbar-track {
  background-color: #f1f1f1;
  border-radius: 10px;
}

form input:focus,
select:focus,
textarea:focus {
  border: 3px solid #6439f2;
  outline: none;
}

dialog button {
  justify-self: center;
  padding: 0.5rem 2rem;

  border-radius: 30px;
  border: none;
  cursor: pointer;
}

dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(3px);
}

.submit-todo-btn,
.submit-edit-todo-btn,
.submit-project-btn,
.submit-edit-project-btn,
.cancel-todo-btn,
.cancel-edit-todo-btn,
.cancel-project-btn,
.cancel-edit-project-btn {
  color: #fff;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);
}

.submit-todo-btn,
.submit-edit-todo-btn,
.submit-project-btn,
.submit-edit-project-btn {
  margin-top: 2rem;
  background-color: #6439f2;
}

.cancel-todo-btn,
.cancel-edit-todo-btn,
.cancel-project-btn,
.cancel-edit-project-btn {
  background-color: #d81159;
}

.submit-todo-btn:hover,
.submit-edit-todo-btn:hover,
.submit-project-btn:hover,
.submit-edit-project-btn:hover,
.cancel-todo-btn:hover,
.cancel-edit-todo-btn:hover,
.cancel-project-btn:hover,
.cancel-edit-project-btn:hover {
  transform: translateY(-3px);
  cursor: pointer;
}

#edit-todo-current-project,
#edit-current-project {
  background-color: rgb(17, 17, 17);
  border: 3px solid rgb(17, 17, 17);
  color: #fff;
}

form input[type='checkbox'] {
  background-color: #fff;
  border-radius: 8px;
}

form input[type='checkbox']::before {
  background-color: black;
}

/*
---------------
Media Queries
---------------
*/

@media (max-width: 850px) {
  body {
    grid-template-columns: 1fr;
  }

  .main-heading-container {
    justify-content: space-around;
  }

  .project-header {
    gap: 1rem;
  }

  .todo-container {
    grid-template-columns: auto 1fr;
    align-items: center;
    gap: 0.5rem;
    padding: 1rem;
  }

  .date,
  .todo-icons-container {
    grid-column-start: 2;
  }

  .todo-icons-container {
    justify-self: end;
  }

  /* navigation */
  nav {
    /* need this to toggle active class*/
    display: none;

    flex-direction: column;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 999;
    min-height: 100vh;
    min-width: 70%;
    padding-top: 100px;

    box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);
  }

  nav.active-nav {
    display: flex;
  }

  .navbar-toggle {
    display: block;
    z-index: 1000;
  }

  /* turn bars into X */
  .navbar-toggle.active-nav .bar:nth-child(2) {
    opacity: 0;
  }

  .navbar-toggle.active-nav .bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
    background-color: #d81159;
  }

  .navbar-toggle.active-nav .bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
    background-color: #d81159;
  }
}
