AAe;EACf;IACE,oCAAoC;IACpC,aAAa;;IAEb,sBAAsB;IACtB,kBAAkB;IAClB,MAAM;IACN,QAAQ;IACR,YAAY;IACZ,iBAAiB;IACjB,cAAc;IACd,kBAAkB;;IAElB,0CAA0C;EAC5C;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,cAAc;IACd,aAAa;EACf;;EAEA,qBAAqB;EACrB;IACE,UAAU;EACZ;;EAEA;IACE,wCAAwC;IACxC,yBAAyB;EAC3B;;EAEA;IACE,0CAA0C;IAC1C,yBAAyB;EAC3B;AACF","sourcesContent":["/*\n---------------\nCSS Reset https://www.joshwcomeau.com/css/custom-css-reset/\n---------------\n*/\n\n/* 1. Use a more-intuitive box-sizing model */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n/* 2. Remove default margin */\n*:not(dialog) {\n  margin: 0;\n}\n\n/* 3. Enable keyword animations */\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  /* 4. Add accessible line-height */\n  line-height: 1.5;\n  /* 5. Improve text rendering */\n  -webkit-font-smoothing: antialiased;\n}\n\n/* 6. Improve media defaults */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\n/* 7. Inherit fonts for form controls */\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\n/* 8. Avoid text overflows */\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\n/* 9. Improve line wrapping */\np {\n  text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n/*\n  10. Create a root stacking context\n*/\n#root,\n#__next {\n  isolation: isolate;\n}\n\nli {\n  list-style-type: none;\n}\n\na {\n  text-decoration: none;\n}\n\n/*\n---------------\nFont size variables\n---------------\n*/\n\n/* @link https://utopia.fyi/type/calculator?c=320,16,1.2,1728,20,1.25,5,2,&s=0.75|0.5|0.25,1.5|2|3|4|6,s-l&g=s,l,xl,12 */\n:root {\n  /* Step -2: 11.1111px  12.8px */\n  --step--2: clamp(0.6944rem, 0.6705rem + 0.1199vw, 0.8rem);\n  /* Step -1: 13.3333px  16px */\n  --step--1: clamp(0.8333rem, 0.7955rem + 0.1894vw, 1rem);\n  /* Step 0: 16px  20px */\n  --step-0: clamp(1rem, 0.9432rem + 0.2841vw, 1.25rem);\n  /* Step 1: 19.2px  25px */\n  --step-1: clamp(1.2rem, 1.1176rem + 0.4119vw, 1.5625rem);\n  /* Step 2: 23.04px  31.25px */\n  --step-2: clamp(1.44rem, 1.3234rem + 0.5831vw, 1.9531rem);\n  /* Step 3: 27.648px  39.0625px */\n  --step-3: clamp(1.728rem, 1.5659rem + 0.8107vw, 2.4414rem);\n  /* Step 4: 33.1776px  48.8281px */\n  --step-4: clamp(2.0736rem, 1.8513rem + 1.1115vw, 3.0518rem);\n  /* Step 5: 39.8131px  61.0352px */\n  --step-5: clamp(2.4883rem, 2.1869rem + 1.5072vw, 3.8147rem);\n  /* Step 6: 47.7757px  76.2939px */\n  --step-6: clamp(2.986rem, 2.5809rem + 2.0254vw, 4.7684rem);\n}\n\n/*\n---------------\nFont\n---------------\n*/\n\n/*! Generated by Font Squirrel (https://www.fontsquirrel.com) on October 7, 2025 */\n\n@font-face {\n  font-family: 'reenie_beanieregular';\n  src: url('../assets/fonts/reeniebeanie-regular-webfont.woff2') format('woff2');\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'robotoregular';\n  src: url('../assets/fonts/roboto-variablefont_wdthwght-webfont.woff2')\n    format('woff2');\n  font-weight: normal;\n  font-style: normal;\n}\n\n/*\n---------------\nLayout \n---------------\n*/\n\nbody {\n  display: grid;\n  grid-template-columns: 1fr 3fr;\n  grid-template-rows: auto 1fr auto;\n  min-height: 100vh;\n  max-width: 100vw;\n\n  font-family: 'robotoregular', sans-serif;\n  font-size: var(--step-0);\n}\n\n/* main heading */\n\n.main-heading-container {\n  grid-column: span 2;\n\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 1rem;\n\n  background: #7189ff;\n  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);\n}\n\n.main-heading-and-icon {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 1rem;\n  padding: 1rem;\n}\n\n.main-heading-container h1 {\n  font-family: 'reenie_beanieregular', sans-serif;\n  font-size: var(--step-6);\n  font-weight: 500;\n  text-align: center;\n}\n\n.main-heading-container .list-icon {\n  width: 100px;\n}\n\n/*\n---------------\nNavigation\n---------------\n*/\n\n/* hamburger on main heading */\n\n.navbar-toggle {\n  display: none;\n  background: transparent;\n  padding: 10px;\n  border: none;\n  cursor: pointer;\n}\n\n.bar {\n  display: block;\n  width: 25px;\n  height: 3px;\n  margin: 5px auto;\n\n  background-color: #111;\n  transition: all 0.3 ease-in-out;\n}\n\n/* nav on left side */\n\nnav {\n  padding: 2rem;\n\n  background: #624cab;\n  color: #fff;\n}\n\nnav h2 {\n  font-size: var(--step-2);\n  font-weight: 500;\n\n  border-bottom: 2px solid #fff;\n}\n\n.add-todo-icon-and-btn,\n.add-project-icon-and-btn {\n  margin: 1.25rem 0;\n}\n\n.add-todo-btn,\n.add-project-btn {\n  font-size: var(--step-1);\n  font-weight: 700;\n}\n\n.nav-project-heading {\n  margin-top: 4rem;\n}\n\nnav .feather-icon {\n  width: 32px;\n  height: 32px;\n}\n\n.feather-icon {\n  cursor: pointer;\n}\n\nnav button {\n  background: transparent;\n  border: none;\n  color: #fff;\n  cursor: pointer;\n}\n\nnav li {\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n  margin: 0.25rem 0;\n  padding: 0.5rem 1rem;\n  width: max-content;\n\n  border-radius: 30px;\n}\n\nnav li:hover {\n  background-color: #7189ff;\n  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);\n}\n\n.add-todo-icon-and-btn:hover,\n.add-project-icon-and-btn:hover {\n  background-color: #d81159;\n}\n\n.active-btn-container.active {\n  background-color: #7189ff;\n  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);\n}\n\n/*\n---------------\nTodos Content\n---------------\n*/\n\n.category-heading {\n  margin: 3rem 0;\n  text-align: center;\n  font-size: var(--step-4);\n\n  color: #624cab;\n}\n\n.project-header {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 2rem;\n  margin: 3rem 0 2rem 0;\n\n  font-family: 'reenie_beanieregular', sans-serif;\n}\n\n.project-heading {\n  font-size: var(--step-4);\n  font-weight: 500;\n}\n\n.todo-container {\n  display: grid;\n  grid-template-columns: auto 1fr auto auto;\n  align-items: center;\n  gap: 2rem;\n  padding: 1rem 2rem;\n\n  border-bottom: 2px solid #a0ddff;\n}\n\n.todo-icons-container,\n.project-header-icons-container {\n  display: flex;\n  gap: 0.5rem;\n}\n\n.completed {\n  color: #c1cefe;\n  text-decoration: line-through;\n}\n\ninput[type='checkbox'] {\n  appearance: none;\n  color: currentColor;\n  width: 1.5em;\n  height: 1.5em;\n  border: 0.15em solid currentColor;\n  border-radius: 50%;\n  transform: translateY(-0.075em);\n  display: grid;\n  place-content: center;\n\n  cursor: pointer;\n}\n\ninput[type='checkbox']::before {\n  content: '';\n  width: 0.65em;\n  height: 0.65em;\n  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);\n  transform: scale(0);\n  transform-origin: bottom left;\n  transition: 120ms transform ease-in-out;\n  box-shadow: inset 1em 1em var(--form-control-color);\n  background-color: #c1cefe;\n}\n\ninput[type='checkbox']:checked::before {\n  transform: scale(1);\n}\n\n.important {\n  color: #d81159;\n}\n\n.completed {\n  color: #c1cefe;\n  text-decoration: line-through;\n}\n\n.description-container {\n  background-color: #a0ddff;\n  padding: 1rem 2rem;\n}\n\n.hide {\n  display: none;\n}\n\n.chevron-icon-container {\n  transition: transform 0.3s ease; /* smooth transition for the flip in class .flip-icon */\n}\n\n.flip-icon {\n  transform: scaleY(-1); /* vertical flip */\n}\n\n.empty {\n  text-align: center;\n}\n\n#content {\n  margin-bottom: 3rem;\n}\n\n/*\n---------------\nFooter\n---------------\n*/\n\nfooter {\n  grid-column: span 2;\n  padding: 0.5rem;\n  text-align: center;\n\n  background-color: #c1cefe;\n}\n\nfooter a {\n  color: #fff;\n}\n\n/*\n---------------\nForms\n---------------\n*/\n\ndialog {\n  padding: 2rem;\n\n  border-radius: 30px;\n  border: none;\n  background-color: #7189ff;\n  color: #fff;\n  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);\n}\n\nform {\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n}\n\n.form-group {\n  display: grid;\n  grid-template-columns: auto 1fr;\n  gap: 0.5rem;\n}\n\nform h3 {\n  margin-bottom: 1rem;\n  font-size: var(--step-2);\n  text-align: center;\n}\n\ninput,\nselect,\ntextarea {\n  padding: 0.15rem 0.5rem;\n\n  border-radius: 30px;\n  border: 3px solid #fff;\n}\n\ntextarea {\n  border-radius: 10px;\n}\n\ntextarea::-webkit-scrollbar {\n  width: 8px;\n}\n\ntextarea::-webkit-scrollbar-thumb {\n  background-color: #c1cefe;\n  border-radius: 10px;\n}\n\ntextarea::-webkit-scrollbar-track {\n  background-color: #f1f1f1;\n  border-radius: 10px;\n}\n\nform input:focus,\nselect:focus,\ntextarea:focus {\n  border: 3px solid #6439f2;\n  outline: none;\n}\n\ndialog button {\n  justify-self: center;\n  padding: 0.5rem 2rem;\n\n  border-radius: 30px;\n  border: none;\n  cursor: pointer;\n}\n\ndialog::backdrop {\n  background-color: rgba(0, 0, 0, 0.5);\n  backdrop-filter: blur(3px);\n}\n\n.submit-todo-btn,\n.submit-edit-todo-btn,\n.submit-project-btn,\n.submit-edit-project-btn,\n.cancel-todo-btn,\n.cancel-edit-todo-btn,\n.cancel-project-btn,\n.cancel-edit-project-btn {\n  color: #fff;\n  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);\n}\n\n.submit-todo-btn,\n.submit-edit-todo-btn,\n.submit-project-btn,\n.submit-edit-project-btn {\n  margin-top: 2rem;\n  background-color: #6439f2;\n}\n\n.cancel-todo-btn,\n.cancel-edit-todo-btn,\n.cancel-project-btn,\n.cancel-edit-project-btn {\n  background-color: #d81159;\n}\n\n.submit-todo-btn:hover,\n.submit-edit-todo-btn:hover,\n.submit-project-btn:hover,\n.submit-edit-project-btn:hover,\n.cancel-todo-btn:hover,\n.cancel-edit-todo-btn:hover,\n.cancel-project-btn:hover,\n.cancel-edit-project-btn:hover {\n  transform: translateY(-3px);\n  cursor: pointer;\n}\n\n#edit-todo-current-project,\n#edit-current-project {\n  background-color: rgb(17, 17, 17);\n  border: 3px solid rgb(17, 17, 17);\n  color: #fff;\n}\n\nform input[type='checkbox'] {\n  background-color: #fff;\n  border-radius: 8px;\n}\n\nform input[type='checkbox']::before {\n  background-color: black;\n}\n\n/*\n---------------\nMedia Queries\n---------------\n*/\n\n@media (max-width: 850px) {\n  body {\n    grid-template-columns: 1fr;\n  }\n\n  .main-heading-container {\n    justify-content: space-around;\n  }\n\n  .project-header {\n    gap: 1rem;\n  }\n\n  .todo-container {\n    grid-template-columns: auto 1fr;\n    align-items: center;\n    gap: 0.5rem;\n    padding: 1rem;\n  }\n\n  .date,\n  .todo-icons-container {\n    grid-column-start: 2;\n  }\n\n  .todo-icons-container {\n    justify-self: end;\n  }\n\n  /* navigation */\n  nav {\n    /* need this to toggle active class*/\n    display: none;\n\n    flex-direction: column;\n    position: absolute;\n    top: 0;\n    right: 0;\n    z-index: 999;\n    min-height: 100vh;\n    min-width: 70%;\n    padding-top: 100px;\n\n    box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.4);\n  }\n\n  nav.active-nav {\n    display: flex;\n  }\n\n  .navbar-toggle {\n    display: block;\n    z-index: 1000;\n  }\n\n  /* turn bars into X */\n  .navbar-toggle.active-nav .bar:nth-child(2) {\n    opacity: 0;\n  }\n\n  .navbar-toggle.active-nav .bar:nth-child(1) {\n    transform: translateY(8px) rotate(45deg);\n    background-color: #d81159;\n  }\n\n  .navbar-toggle.active-nav .bar:nth-child(3) {\n    transform: translateY(-8px) rotate(-45deg);\n    background-color: #d81159;\n  }\n}\n"],"sourceRoot":""}]);
